import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { Observable } from 'rxjs';
import { API_CONFIG } from 'src/app/config/api.config';
import { RegraNewDTO } from 'src/app/models/regra/regra-new.dto';
import { RegraUpdateDTO } from 'src/app/models/regra/regra-update.dto';

@Injectable({
  providedIn: 'root'
})
export class RegraService {

  constructor(private http: HttpClient, private storage: StorageService) { }

  findRegraById(idRegra: string): Observable<any> {
    return this.http.get(
      `${API_CONFIG.baseUrl}sistema/${idRegra}`,
      {
        responseType: 'json'
      }
    );
  }

  update = (dto: RegraUpdateDTO): Observable<any> => {
    return this.http.put(`${API_CONFIG.baseUrl}sistema`,
      dto,
      {
        responseType: 'json'
      }

    );
  }

  insert(obj: RegraNewDTO): Observable<any> {
    return this.http.post(
      `${API_CONFIG.baseUrl}sistema`, obj, {
      responseType: "json",
      observe: "body"
    });
  }

  delete(id: string): Observable<any> {
    return this.http.delete(`${API_CONFIG.baseUrl}sistema/${id}`,
      {
        responseType: 'json',
        observe: "body"
      });
  }

  
}
