import { Component, OnInit } from '@angular/core';
import { RpgService } from 'src/app/services/rpg/rpg.service';
import { NpcService } from 'src/app/services/npc/npc.service';
import { PaginacaoService } from 'src/app/services/utils/paginacao.service';
import { MessageService } from 'primeng/api';
import { FichaModeloService } from 'src/app/services/fichas/ficha-modelo.service';
import { RegraService } from 'src/app/services/regra/regra.service';
import { RegraNewDTO } from 'src/app/models/regra/regra-new.dto';
import { AlertController } from '@ionic/angular';
import { Router } from '@angular/router';

@Component({
  selector: 'app-rpg',
  templateUrl: './rpg.component.html',
  styleUrls: ['./rpg.component.scss'],
})
export class RpgComponent implements OnInit {

  estagioAtual = 0;

  showLista = false;
  showNovo = false;

  paginaAtual = 0;
  loading: boolean = false;

  opcao: number = 0;

  tipoNPC: number = -1;
  idAliados: string[] = [];
  idInimigos: string[] = [];
  idNeutros: string[] = [];

  tipoFaccao: number = -1;
  idFaccoesBoas: string[] = [];
  idFaccoesNeutras: string[] = [];
  idFaccoesHostis: string[] = [];

  idCapitulos: string[] = [];

  idFichaModelo = '';
  fmEncontrada: boolean = false;

  nomeRegra: string = '';
  descricaoRegra: string = '';
  idRegras: string[] = [];

  idMapas: string[] = [];

  adicionar: boolean = true;

  constructor(private router: Router,
    private alertController: AlertController,
    private rpgService: RpgService,
    private npcService: NpcService,
    private paginaService: PaginacaoService,
    private toast: MessageService,
    private fmService: FichaModeloService,
    private regraService: RegraService) { }

  ngOnInit() { }

  monitor(evt) {
    this.estagioAtual++;
    this.paginaAtual = 0;
    this.showLista = false;
    this.showNovo = false;
    if (this.estagioAtual == 5) {
      const p = this.fmService.findPageByCriador(this.paginaAtual.toString());
      this.paginaService.setPromisse(p);
    }
    //this.rpgService.check();
  }

  saveMapas(evt) {
    this.rpgService.insertMapas(this.idMapas);
    this.monitor(evt);
  }

  getMapas(evt) {
    this.addListaMapa(evt);
  }

  addListaMapa(evt) {
    const isSelected = this.isOnArray(evt.id, this.idMapas);
    if (!isSelected) {
      this.idMapas.push(evt.id);
      this.callAdd();
    } else {
      this.callFail(evt.id);
    }

  }

  getValidacao(): boolean {
    const nome = this.nomeRegra.length < 5;
    const descricao = this.descricaoRegra.length < 5;
    if (nome || descricao) {
      return true;
    } else {
      return false;
    }
  }

  addRegra() {
    this.loading = true;

    const regra: RegraNewDTO = {
      nome: this.nomeRegra,
      descricao: this.descricaoRegra
    }
    this.regraService.insert(regra)
      .subscribe(res => {
        this.loading = false;
        this.idRegras.push(res.idSistema);
      },
        error => {
          this.loading = false;
        });
  }

  regraDeletada(evt) {
    this.loading = true;
    this.regraService.delete(evt)
      .subscribe(res => { this.loading = false; },
        error => { this.loading = false });
  }

  saveRegras(evt) {
    const p = this.rpgService.insertRegras(this.idRegras);
    this.monitor(evt);
    this.persisteRPG(p);
  }

  buscarFicha() {
    this.loading = true;
    this.fmService.findFichaModeloById(this.idFichaModelo)
      .subscribe(res => {
        this.loading = false;
        this.callFindIt()
        this.fmEncontrada = true;
      },
        error => {
          this.loading = false;
          this.fmEncontrada = false;
          this.callFindNot();
        });
  }

  saveFM(evt) {
    this.rpgService.insertFichaModelo(this.idFichaModelo);
    this.monitor(evt);
  }

  saveCapitulos(evt) {
    this.rpgService.insertCapitulo(this.idCapitulos);
    this.monitor(evt);
  }

  getCapitulo(evt) {
    this.addListaCapitulo(evt);
  }

  addListaCapitulo(evt) {
    const isSelected = this.isOnArray(evt.id, this.idCapitulos);
    if (!isSelected) {
      this.idCapitulos.push(evt.id);
      this.callAdd();
    } else {
      this.callFail(evt.id);
    }

  }

  saveFaccoes(evt) {
    this.rpgService.insertFaccoesBoas(this.idFaccoesBoas);
    this.rpgService.insertFaccoesNeutras(this.idFaccoesNeutras);
    this.rpgService.insertFaccoesHostis(this.idFaccoesHostis);
    this.monitor(evt);
  }

  getFaccao(evt) {
    this.addListaFaccao(evt);
  }

  addListaFaccao(evt) {
    const isAliado = this.isOnArray(evt.id, this.idFaccoesBoas);
    const isNeutro = this.isOnArray(evt.id, this.idFaccoesNeutras);
    const isInimigo = this.isOnArray(evt.id, this.idFaccoesHostis);

    if (!(isAliado || isNeutro || isInimigo)) {
      switch (Number(this.tipoFaccao)) {
        case 0:
          this.idFaccoesBoas.push(evt.id);
          this.callAdd();
          break;

        case 1:
          this.idFaccoesNeutras.push(evt.id);
          this.callAdd();
          break;

        case 2:
          this.idFaccoesHostis.push(evt.id);
          this.callAdd();
          break;

        default:
          this.callSelecione();
          break;

      }
    } else {
      this.callFail(evt.id);
    }

  }

  saveNpcs(evt) {
    this.rpgService.insertAliados(this.idAliados);
    this.rpgService.insertInimigos(this.idInimigos);
    this.rpgService.insertNeutros(this.idNeutros);
    this.monitor(evt);
  }

  addListaNpc(evt) {
    const isAliado = this.isOnArray(evt.id, this.idAliados);
    const isNeutro = this.isOnArray(evt.id, this.idNeutros);
    const isInimigo = this.isOnArray(evt.id, this.idInimigos);

    if (!(isAliado || isNeutro || isInimigo)) {
      switch (Number(this.tipoNPC)) {
        case 0:
          this.idAliados.push(evt.id);
          this.callAdd();
          break;

        case 1:
          this.idNeutros.push(evt.id);
          this.callAdd();
          break;

        case 2:
          this.idInimigos.push(evt.id);
          this.callAdd();
          break;

        default:
          this.callSelecione();
          break;

      }
    } else {
      this.callFail(evt.id);
    }

  }

  isOnArray(id: string, arr) {
    return arr.includes(id);
  }

  getNotification(evt) {
    if (this.paginaAtual != evt) {
      this.paginaAtual = evt;
      this.consultar();
    }
  }

  consultar() {
    let p = null;
    switch (this.opcao) {
      case 0: // NPC
        p = this.npcService.findPageByCriador(this.paginaAtual.toString());
        break;
    }
    this.paginaService.setPromisse(p);
  }

  novo() {
    this.showNovo = !this.showNovo;
    this.showLista = false;
  }

  listar() {
    this.showNovo = false;
    this.showLista = true;
    this.consultar();
  }

  callAdd() {
    this.toast.add({
      severity: 'success',
      summary: 'Adicionado',
      detail: 'Adicionado com sucesso!'
    });
  }

  callFindIt() {
    this.toast.add({
      severity: 'success',
      summary: 'Encontrado',
      detail: 'Modelo de Ficha encontrado'
    });
  }

  callFindNot() {
    this.toast.add({
      severity: 'error',
      summary: 'Não encontrada',
      detail: 'Modelo de Ficha  NÃO encontrado'
    });
  }

  callFail(id) {
    this.toast.add({
      severity: 'error',
      summary: 'Já incluso',
      detail: id + ' já está incluso(a)!'
    });
  }

  callSelecione() {
    this.toast.add({
      severity: 'error',
      summary: 'Selecione uma opção!',
      detail: 'Selecione antes de prosseguir!'
    });
  }

  persisteRPG(p) {
    this.loading = true;
    p.subscribe(res => {
      this.presentAdicionar(res.idRpg);
      this.loading = false;
      this.router.navigateByUrl('home');
    },
      error => {
        this.loading = false;
      });
  }

  async presentAdicionar(id: string) {
    const alert = await this.alertController.create({
      header: 'Criado com sucesso!',
      message: 'Código: ' + id,
      buttons: [
        {
          text: 'Ok',
          role: 'cancel',
          cssClass: 'secondary',
          handler: () => {
            this.copyToClipboard(id);
          }
        }
      ]
    });

    await alert.present();
  }

  async copyToClipboard(value: string) {
    let id = (e: ClipboardEvent) => {
      e.preventDefault();
      e.clipboardData.setData('text', (value));

    };
    document.addEventListener('copy', id);
    document.execCommand('copy');
    document.removeEventListener('copy', null);
  }

  getCode(evt) {
    console.log(evt);
    this.idFichaModelo = evt.id;
    this.buscarFicha();
  }

  listenValue(evt){
    this.fmEncontrada = false;
    //console.log('Mudou: ', evt);
  }

}
