import { Injectable } from '@angular/core';
import { HttpClient, HttpParams } from '@angular/common/http';
import { StorageService } from '../storage-service.service';
import { API_CONFIG } from 'src/app/config/api.config';
import { Observable } from 'rxjs';
import { RpgNewDTO } from 'src/app/models/rpg/rpg-new.dto';
import { RpgBasicDTO } from 'src/app/models/rpg/rpg-basic.dto';

@Injectable({
  providedIn: 'root'
})
export class RpgService {

  private newRpg: RpgNewDTO

  constructor(private http: HttpClient, private storage: StorageService) {
    this.newRpg = {
      descricao: undefined,
      historia: undefined,
      idAliados: undefined,
      idCapitulos: undefined,
      idFaccoesBoas: undefined,
      idFaccoesHostis: undefined,
      idFaccoesNeutras: undefined,
      idFichaModelo: undefined,
      idInimigos: undefined,
      idMapas: undefined,
      idNeutros: undefined,
      idSistemas: undefined,
      nome: undefined,
      tipoRPG: undefined,
      imgURL: undefined
    }
  }

  insertInfo(obj: RpgBasicDTO) {
    this.newRpg.nome = obj.nome;
    this.newRpg.descricao = obj.descricao;
    this.newRpg.historia = obj.historia;
    this.newRpg.tipoRPG = obj.tipoRPG;
    this.newRpg.imgURL = obj.imgURL;
  }

  insertAliados(idAliados: string[]) {
    this.newRpg.idAliados = idAliados;
  }

  insertInimigos(idInimigos: string[]) {
    this.newRpg.idInimigos = idInimigos;
  }

  insertNeutros(idNeutros: string[]) {
    this.newRpg.idNeutros = idNeutros;
  }

  insertFaccoesBoas(idFaccoesBoas: string[]) {
    this.newRpg.idFaccoesBoas = idFaccoesBoas;
  }

  insertFaccoesNeutras(idFaccoesNeutras: string[]) {
    this.newRpg.idFaccoesNeutras = idFaccoesNeutras;
  }

  insertFaccoesHostis(idFaccoesHostis: string[]) {
    this.newRpg.idFaccoesHostis = idFaccoesHostis;
  }

  insertCapitulo(idCapitulo: string[]) {
    this.newRpg.idCapitulos = idCapitulo;
  }

  insertFichaModelo(idFichaModelo: string) {
    this.newRpg.idFichaModelo = idFichaModelo;
  }

  insertRegras(idRegras: string[]) {
    this.newRpg.idSistemas = idRegras;
    return this.insert(this.newRpg);
  }

  insertMapas(idMapas: string[]) {
    this.newRpg.idMapas = idMapas;
  }

  private insert(obj: RpgNewDTO): Observable<any> {
    return this.http.post(
      `${API_CONFIG.baseUrl}rpg`, obj, {
      responseType: "json",
      observe: "body"
    });
  }

  check() {
    console.log(this.newRpg);
  }

  findPageByTipoRPG(tipoRPG: string, page: string = '0', linesPerPage: string = '10'): Observable<any> {
    console.log(tipoRPG)
    const params = new HttpParams()
      .set('tipoRPG', tipoRPG)
      .set('page', page)
      .set('linesPerPage', linesPerPage);

    return this.http.get(`${API_CONFIG.baseUrl}rpg/page`,
      {
        params,
        observe: 'body',
      });
  }
}
