import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { RpgBasicDTO } from 'src/app/models/rpg/rpg-basic.dto';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { AlertController } from '@ionic/angular';
import { StorageService } from 'src/app/services/storage-service.service';
import { RpgService } from 'src/app/services/rpg/rpg.service';

@Component({
  selector: 'app-rpg-form',
  templateUrl: './rpg-form.component.html',
  styleUrls: ['./rpg-form.component.scss'],
})
export class RpgFormComponent implements OnInit {

  formRpg: FormGroup;
  newRpgForm: RpgBasicDTO;

  @Output('prosseguir')
  prosseguir: EventEmitter<any> = new EventEmitter();
  proximo() {
    this.newRpgForm = {
      descricao: this.formRpg.get('descricao').value,
      historia: this.formRpg.get('historia').value,
      nome: this.formRpg.get('nome').value,
      tipoRPG: this.formRpg.get('tipoRPG').value,
      imgURL: this.formRpg.get('imgURL').value
    }
    this.rpgService.insertInfo(this.newRpgForm);
    this.prosseguir.emit('PRÓXIMO!');
  }

  constructor(private formBuilder: FormBuilder, private storage: StorageService,
    private alertController: AlertController, private rpgService: RpgService) {
    this.formRpg = this.formBuilder.group({
      nome: ['', [Validators.required]],
      descricao: ['', [Validators.required]],
      historia: ['', [Validators.required]],
      tipoRPG: ['', [Validators.required]],
      imgURL: []
    });
  }

  ngOnInit() { }

}
